@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body,
h1,
div,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

select {
  appearance: none;
}

select:focus,
input:focus,
button:focus {
  outline: none;
  border: none;
}

div {
  outline: none;
}

select::-ms-expand {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.preferences__wrapper {
  padding: 32px 32px 141px;
  background-color: #f8f8f8;
  max-width: 986px;
  margin: 0 auto;
}
.preferences__heading {
  padding-bottom: 32px;
}
.preferences__title {
  font-weight: 500;
  font-size: 28px;
  line-height: 32px;
  color: #0b0a0a;
  padding-bottom: 4px;
}
.preferences__subtitle {
  font-size: 14px;
  line-height: 16px;
  color: #585050;
}

.form {
  display: flex;
  flex-direction: column;
  max-width: 320px;
}
@media (min-width: 768px) {
  .form {
    display: grid;
    grid-template-columns: 120px 1fr;
    gap: 24px 4px;
  }
}
.form__label {
  font-size: 14px;
  line-height: 16px;
  max-width: 196px;
  color: #585050;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .form__label {
    flex-direction: row;
    max-width: 320px;
    justify-content: space-between;
    align-items: center;
  }
}
.form__input {
  padding: 8px;
  margin-top: 10px;
  max-width: 196px;
  background-color: #dfdada;
  border: 1px solid #887c7c;
  border-radius: 6px;
  cursor: pointer;
}
.form__input:focus {
  border: 1px solid #2a75db;
}
.form__input:hover {
  background-color: #887c7c;
  border: 1px solid #585050;
}
.form__select {
  padding: 8px;
  margin-top: 10px;
  max-width: 196px;
  background-color: #dfdada;
  border: 1px solid #887c7c;
  border-radius: 6px;
  cursor: pointer;
  outline: none;
}
.form__select:focus {
  border: 1px solid #2a75db;
}
.form__select:focus {
  border: 1px solid #2a75db;
}
@media (min-width: 768px) {
  .form__select {
    flex-direction: row;
    max-width: 196px;
    min-width: 196px;
    margin-top: 0;
    justify-content: space-between;
    align-items: center;
  }
}
.form__theme {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 8px;
}
@media (min-width: 768px) {
  .form__theme {
    padding-top: 20px;
  }
}
.form__buttons {
  padding-top: 32px;
  display: flex;
  gap: 16px;
}
.form__button {
  display: block;
  cursor: pointer;
  padding: 4px 8px;
  background: #2a75db;
  border-radius: 3px;
  border: 1px solid transparent;
  color: #f8f8f8;
  font-size: 14px;
  line-height: 24px;
  min-width: 115px;
}
.form__button:hover {
  box-shadow: inset 0 0 0 100px rgba(0, 0, 0, 0.2);
}
.form__button:focus {
  border: 1px solid #aac9f1;
}

.form__label:not(:first-child) {
  padding-top: 20px;
}

.form__label--arrow {
  cursor: pointer;
  position: absolute;
  content: "";
  top: 24px;
  right: 14px;
  display: inline-flex;
  width: 10px;
  height: 10px;
  border-top: 3px solid #887c7c;
  border-right: 3px solid #887c7c;
  transition: all 0.25s ease;
  transform: rotate(135deg);
}
.form__label--arrow:focus {
  border-top: 3px solid #2a75db;
  border-right: 3px solid #2a75db;
}
@media (min-width: 768px) {
  .form__label--arrow {
    top: 14px;
    right: 14px;
  }
}

.active {
  transform: rotate(315deg);
  border-top: 3px solid #2a75db;
  border-right: 3px solid #2a75db;
}

.form__select--active {
  background: #dfdada;
}

.form__label--flex {
  flex-direction: row;
  align-items: center;
  gap: 8px;
}

.form__label--padding {
  padding-bottom: 10px;
  padding-top: 20px;
}
@media (min-width: 768px) {
  .form__label--padding {
    margin-top: -20px;
    padding-top: 0;
  }
}

.form__theme label {
  cursor: pointer;
}

.form__input--code {
  min-width: 49px;
  max-width: 49px;
  padding-left: 4px;
  padding-right: 4px;
}

.form__input--radio {
  display: flex;
  align-items: baseline;
}

.form__button--submit {
  background: #2a75db;
  color: #f8f8f8;
}

.form__button--reset {
  background: transparent;
  color: #2a75db;
  max-width: 60px;
  min-width: 60px;
}
.form__button--reset:hover {
  background: #f8f8f8;
  color: #205eb0;
  box-shadow: inset 0 0 0 100px rgba(0, 0, 0, 0.2);
}
.form__button--reset:focus {
  border: 1px solid #2a75db;
}

.form__label--placeholder {
  position: relative;
}

.code-placeholder {
  position: absolute;
  font-size: 11px;
  line-height: 13px;
  color: #887c7c;
  bottom: -65px;
  left: 0;
  min-width: 120px;
}
@media (min-width: 768px) {
  .code-placeholder {
    left: 126px;
    bottom: -14px;
  }
}

.select--hidden {
  display: none;
}

input[type=radio] {
  position: relative;
  height: 24px;
  width: 24px;
  appearance: none;
  outline: none;
}

input[type=radio]::before {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 8px;
  left: 0;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 2px solid #2a75db;
}

input[type=radio]:checked::before {
  top: 8px;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: #2a75db;
  box-shadow: inset 0 0 0 2px #f8f8f8;
}

.select {
  min-width: 196px;
  max-width: 196px;
  position: relative;
}
.select__list {
  position: absolute;
  min-width: 196px;
  max-width: 196px;
  z-index: 1;
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  border-radius: 6px;
  background: #f8f8f8;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.select__option {
  background: transparent;
  border: none;
  width: 100%;
  cursor: pointer;
  max-height: 0;
}
.select__current {
  cursor: pointer;
}
.select__item {
  cursor: pointer;
  padding: 8px;
  border: none;
  max-width: 100%;
  background: #f8f8f8;
}
.select__item:hover {
  background: #dfdada;
}
@media (min-width: 768px) {
  .select__item {
    flex-direction: row;
    min-width: 100%;
    justify-content: space-between;
    align-items: center;
  }
}

.select__list--hidden {
  display: none;
}

.select--open {
  display: block;
}

.dark .preferences__wrapper {
  background-color: #1e1d1d;
  padding: 32px 32px 141px;
  max-width: 986px;
  margin: 0 auto;
}

.dark .preferences__title {
  color: #f3f3f3;
}

.dark .preferences__subtitle {
  color: #dfdada;
}

.dark .form__label {
  color: #dfdada;
}

.dark .form__select,
.dark .form__input {
  color: #f3f3f3;
  background-color: #585050;
  border: 1px solid #887c7c;
}
.dark .form__select:hover,
.dark .form__input:hover {
  box-shadow: inset 0 0 0 100px rgba(255, 255, 255, 0.2);
}
.dark .form__select:focus,
.dark .form__input:focus {
  border: 1px solid #b9d5fc;
}

.dark .form__input:focus {
  border: 1px solid #b9d5fc;
}

.dark .form__button--submit {
  background-color: #b9d5fc;
  color: #1e1d1d;
}
.dark .form__button--submit:hover {
  box-shadow: inset 0 0 0 100px rgba(255, 255, 255, 0.2);
}
.dark .form__button--submit:focus {
  border: 1px solid #f0f6ff;
}

.dark .form__button--reset {
  background-color: #424242;
  color: #aac9f1;
}
.dark .form__button--reset:hover {
  box-shadow: inset 0 0 0 100px rgba(255, 255, 255, 0.2);
}
.dark .form__button--reset:focus {
  border: 1px solid #b9d5fc;
}

.dark .select__list {
  background-color: #424242;
}

.dark .select__item:focus {
  background-color: #585050;
}

.dark .form__input--radio {
  color: #f3f3f3;
}

.dark input[type=radio]::before {
  border: 2px solid #dfdada;
}

.dark input[type=radio]:checked::before {
  background-color: #b9d5fc;
  box-shadow: inset 0 0 0 2px #1e1d1d;
}

.dark .footer__wrapper {
  background-color: #1e1d1d;
}

.dark .footer__text-content {
  color: #dfdada;
}

.dark .footer__link {
  color: #aac9f1;
}

.footer {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  min-height: 256px;
  max-width: 986px;
  margin: 0 auto;
  background-color: #f8f8f8;
}
.footer__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  min-height: 256px;
  background: url("../img/icons/footer-background.svg") 50%/cover no-repeat;
  font-size: 14px;
  line-height: 17px;
  padding-bottom: 24px;
}
.footer__text-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #585050;
}
@media (min-width: 768px) {
  .footer__text-content {
    flex-direction: row;
    gap: 8px;
  }
}
.footer__link {
  color: #205eb0;
  border-bottom: 1px solid #aac9f1;
}

html {
  scroll-behavior: smooth;
}

.page {
  background-color: #b0b5bd;
}

.dark {
  background-color: #0c0d0d;
}

.container {
  min-width: 320px;
  min-height: auto;
}
@media (min-width: 768px) {
  .container {
    min-width: 100%;
  }
}
@media (min-width: 1200px) {
  .container {
    margin: 0 auto;
    min-width: 100%;
  }
}